@import 'assets/scss/utils/mixins';

.expiry-date {
	vertical-align: middle;
	align-items: center;
	display: inline-flex;
	align-items: center;
}

.icon {
	overflow: visible !important;
}
button {
	width: 34px;
	height: 34px;
}

.expired-button {
	border-radius: 4px;
}

div.col.position-relative {
	padding: 0 !important;
}

findqo-button.icon {
	display: block;
	width: 20px !important;
	height: 20px !important;
}

.ad-image {
	width: 100%;
	height: 190px;
	object-fit: cover;
	border-top-left-radius: 4px;
	z-index: -1;
}

.image-container {
	height: 190px;
}

.view-photos {
	bottom: 8px;
	width: unset;
	background-color: rgba(26, 31, 46, 0.65);
	border: 1px solid var(--general-3) !important;

	.fa-image {
		height: 10px !important;
		width: auto !important;
	}

	&:hover {
		background-color: rgba(26, 31, 46, 0.5) !important;
	}
}

findqo-button.phone {
	height: 40px;
}

.desktop-advertisement {
	border: 1px solid var(--general-2);
	border-radius: 4px;
	position: relative;
}

.border-bottom {
	border-bottom: 1px solid var(--general-2);
}

.divider {
	width: 5px;
	height: 5px;
}

.ad-item-divider {
	height: 16px;
	background: var(--general-1);
}

.ad-img-btn {
	width: 35px;
	height: 35px;
	background: var(--general-1);
}

.spotlight {
	inset-inline-end: 0;
	border-radius: 0px 0px 0px 5px;
	border: 1px solid var(--prim-2);
	border-top: none;
}

.timer {
	width: 15px;
	height: 15px;
}

.access-timer {
	width: 18px;
	height: 18px;
	display: inline-block !important;
}

.business-information {
	width: 45px;
	height: 30px;
}

.status {
	padding: 0px 12px;
	height: 25px;
	border: none;
	border-radius: 5px;
	background-color: var(--info-1);
}

.h-50px {
	height: 50px;
}

.h-55px {
	height: 55px;
}

.h-25px {
	height: 25px;
}

.alert-border {
	border-top: 1px solid var(--general-2);
}

.warning-icon {
	width: 20px;
	height: 20px;
}

.help-icon {
	width: 18px;
	height: 18px;
}

findqo-button.retry-payment-btn {
	height: 40px !important;
}

.retry-payment-btn {
	height: 40px !important;
}

@include media-breakpoint-up(sm) {
	findqo-button.phone {
		height: 40px;
		width: 150px;
	}

	button {
		width: 38px;
		height: 38px;
	}

	.image-container {
		width: 250px;
		height: 150px;
	}

	.ad-image {
		width: 250px;
		height: 150px;
	}
}

@include media-breakpoint-down(md) {
	.image-width {
		width: 100%;
	}

	.ad-image {
		border-radius: 0px !important;
	}
}

.tenant-img {
	border: 2px solid var(--basic-1);
	margin-inline-end: -10px;
	border-radius: 100px;
}

.tenant-count {
	width: 36px;
	height: 36px;
	border-radius: 100px;
	line-height: 36px;
	text-align: center;
}

.tenant-list {
	background-color: rgba(255, 90, 184, 0.08);
	padding-top: 10px;
	padding-bottom: 10px;
	border: 1px soild var(--general-1);
}

.on-hunt-badge {
	position: absolute;
	bottom: 0;
}

.avatar {
	width: 36px;
	height: 36px;
	border-radius: 50%;
}

.agent-img {
	border-radius: 50%;
	object-fit: cover;
}
